<template>
  <div class="garage-services">
    <header
      class="text-center d-flex garage-header bg-light p-2 justify-content-between align-center shadow-sm"
    >
      <h5 class="text-center">Services</h5>
      <img
        class="img-fluid"
        src="@/assets/services.png"
        alt="garage-vehicle-image"
      />
    </header>
    <loader :loading="loading" />
    <keep-alive>
      <main>
        <div class="garage-services-lists">
          <div class="service-list-group"></div>
          <p v-if="services" class="text-center my-3">
            Services Found : {{ services.length }}
          </p>
          <ol class="list-group">
            <router-link
              class="row service-list w-100 m-0 p-1 border py-3"
              tag="div"
              :to="'/service/' + service.vehicle.vehicleNo"
              v-for="service in services"
              :key="service.serviceId"
            >
              <div
                class="col-lg-3 col-md-2 col-sm-12 services-list-item d-flex justify-content-between"
              >
                <div class="number-plate">
                  <span class="text-dark me-2">
                    {{ service.vehicle.vehicleNo }}</span
                  >
                  <span v-if="service.vehicle.vehicleModel">
                    - {{ service.vehicle.vehicleModel }}</span
                  >
                </div>
                <div>
                  <span class="badge bg-danger">{{ service.serviceId }} </span>
                  <span class="badge bg-dark ms-2"
                    >{{ service.serviceType }}
                  </span>
                </div>
              </div>
              <div class="col-lg-4 col-md-4 col-sm-12 services-list-item">
                <span class="fw-bold"> Customer Name :</span>
                {{ service.customerName }}
              </div>
              <div class="col-lg-2 col-md-2 col-sm-12 services-list-item">
                <span class="fw-bold">Last Service </span>:
                <span v-if="service.serviceDate">{{
                  service.serviceDate
                }}</span>
                <span v-else>{{
                  service.vehicle.serviceDate
                    .slice(0, 10)
                    .split("-")
                    .reverse()
                    .join("-")
                }}</span>
              </div>
              <div class="col-lg-2 col-md-2 col-sm-12 services-list-item">
                <span v-if="service.serviceTotal" class="fw-bold d-flex"
                  >Bill Number :
                  <span v-if="service.bill" class="fw-normal ms-2 me-auto">
                    {{ service.bill.billNumber }} &nbsp;
                  </span>
                  <span class="border p-1 bg-light">
                    Rs.{{ service.serviceTotal }}</span
                  >
                </span>
                <span v-else class="fw-bold d-flex"
                  >Bill Number :
                  <span v-if="service.bill" class="fw-normal ms-2 me-auto">
                    {{ service.bill.billNumber }} &nbsp;
                  </span>
                  <span> Rs.{{ service.bill.billAmount }}</span>
                </span>
              </div>
            </router-link>
          </ol>
        </div>
      </main>
    </keep-alive>
  </div>
</template>

<script>
import Loader from "../../components/Loader.vue";
import http from "../../common/http-common";
export default {
  components: {
    Loader,
  },
  data() {
    return {
      loading: false,
      services: [],
    };
  },
  async created() {
    this.loading = true;
    const result = await http.get("/services");
    this.services = result.data.services;
    this.loading = false;
  },
};
</script>

<style lang="scss" scoped>
.garage-header {
  img {
    width: 2rem;
    height: 2rem;
  }
}
.number-plate {
  font-family: monospace;
}

.service-list {
  padding: 0.75rem;
  border-radius: 0.5rem;
  cursor: pointer;
  text-decoration: none;
  color: #232526;
  &:hover {
    background: rgb(240, 240, 240);
  }
  &-group {
    border-radius: 0.5rem;
  }
  &-heading {
    margin-bottom: 0.5rem;
    padding: 0.75rem;
    background: #0d6efd;
    color: #fff;
    border-radius: 0.5rem;
    font-weight: bold;
  }
}
</style>
